<template>
  <div class="page-card-content" v-loading="loading">
    <div class="top-area">
      <el-form ref="searchForm" :model="searchForm" inline size="middle" style="width:100%">
        <div style="display:flex">
          <div style="flex:1">
            <el-form-item label="账号">
              <el-input
                v-model="searchForm.account"
                placeholder="输入搜索关键字"
                clearable
                class="mr50"
              >
              </el-input>
            </el-form-item>
            <el-form-item label="角色">
              <el-select v-model="searchForm.role" placeholder="请选择内容" clearable>
                <el-option
                  v-for="(item, index) in rolesAll"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div>
            <el-form-item>
              <el-button type="primary" @click="handleRowClick('add')">新增</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData()">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="resetData()">重置</el-button>
            </el-form-item>
          </div>
        </div>
      </el-form>
    </div>

    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="620"
      @select="handlSelect"
      @select-all="handlSelect"
    >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="账号" show-overflow-tooltip prop="account"> </el-table-column>
      <el-table-column label="手机号码" prop="mobile"> </el-table-column>
      <el-table-column label="角色" prop="roleName"> </el-table-column>
      <el-table-column label="状态" prop="state">
        <template v-slot="scope">
          <span>{{ scope.row.state | filterState }}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建人" prop="creatorName"> </el-table-column>
      <el-table-column label="创建时间" prop="createTime"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="handleRowClick('editor', scope.row)">编辑</el-button>
          <el-button type="text" @click="handleRowClick('enable', scope.row)">{{
            scope.row.state == 0 ? '启用' : '禁用'
          }}</el-button>
          <el-button type="text" @click="handleRowClick('delete', scope.row)" style="color:#f56c6c"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      class="main-pagination"
      :total="pageInfo.total"
      :current-page="searchForm.pageNum"
      :page-size="searchForm.pageSize"
      :page-sizes="pageInfo.pageSizes"
      @current-change="handlePageChanged"
      @size-change="handlePageSizeChanged"
      background
      layout="total, sizes, prev, pager, next"
    />
    <!-- 新增-->
    <el-dialog
      :title="operationType == 'editor' ? '编辑' : '新增'"
      width="580px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="账号" prop="account">
          <el-input
            v-model="addForm.account"
            class="labelWidth"
            clearable
            maxlength="15"
          ></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="addForm.mobile" class="labelWidth" clearable></el-input>
        </el-form-item>
        <el-form-item label="登录密码" prop="password">
          <el-input
            type="password"
            v-model="addForm.password"
            autocomplete="off"
            clearable
            class="labelWidth"
            :disabled="operationType == 'editor'"
          ></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input
            type="password"
            v-model="addForm.checkPass"
            autocomplete="off"
            clearable
            class="labelWidth"
            :disabled="operationType == 'editor'"
          ></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="roleIds">
          <el-select v-model="addForm.roleIds" multiple clearable>
            <el-option
              v-for="(item, index) in rolesAll"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="state">
          <el-radio-group v-model="addForm.state">
            <el-radio :label="1">启用</el-radio>
            <el-radio :label="0">禁用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            保存
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/index.js'
export default {
  filters: {
    filterState(val) {
      return val == 0 ? '禁用' : '启用'
    }
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.addForm.checkPass !== '') {
          this.$refs.addForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      searchForm: {
        account: '',
        role: '',
        pageNum: 1,
        pageSize: 10
      },
      dialogVisible: false,
      operationType: 'add',
      addForm: {
        state: '1',
        account: '',
        mobile: '',
        password: '',
        roleIds: [],
        checkPass: ''
      },
      formRules: {
        type: [
          {
            required: true
          }
        ],
        account: [
          {
            trigger: 'blur',
            message: '请输入正确账号（只支持大小写）',
            pattern: '^[a-zA-Z]',
            required: true
          }
        ],
        mobile: {
          trigger: 'blur',
          message: '请输入正确手机号',
          pattern: '^1[35789]\\d{9}$',
          required: true
        },
        password: [{ validator: validatePass, trigger: 'blur', required: true }],
        checkPass: [{ validator: validatePass2, trigger: 'blur', required: true }],
        roleIds: {
          trigger: 'change',
          message: '请选择角色',
          required: true
        }
      },
      rolesAll: [],
      accountName: []
    }
  },
  created() {},
  mounted() {
    this.getTableData()
    this.getRolesAll()
  },
  methods: {
    // 所有角色
    getRolesAll() {
      API.getRolesAll().then(res => {
        if (res.status == 200) {
          this.rolesAll = res.data
        } else {
          this.$message.error(res.message)
        }
      })
    },
    submitForm(type) {
      if (type == 1) {
        this.$refs.addForm.resetFields()
        this.addForm.desc = ''
        this.dialogVisible = false
      } else {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            if (this.addForm.id) {
              let form = {
                mobile: this.addForm.mobile,
                account: this.addForm.account,
                roleIds: this.addForm.roleIds,
                id: this.addForm.id,
                state: this.addForm.state
              }
              API.getAccountMotiy(form).then(res => {
                if (res.status == 200) {
                  this.$refs.addForm.resetFields()
                  this.dialogVisible = false
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            } else {
              delete this.addForm.checkPass
              API.getAccountAdd(this.addForm).then(res => {
                if (res.status == 200) {
                  this.$refs.addForm.resetFields()
                  this.dialogVisible = false
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            }
          } else {
            this.$message.error('请检查表单内容是否填写完整')
          }
        })
      }
    },
    handlSelect(row) {
      this.clickRow = row
    },
    // 重置
    resetData() {
      this.searchForm = {
        account: '',
        role: '',
        pageNum: 1,
        pageSize: 10
      }
      this.getTableData()
    },
    handleRowClick(type, row) {
      switch (type) {
        case 'add':
          this.dialogVisible = true
          this.operationType = 'add'
          if (this.addForm.id) {
            delete this.addForm.id
          }
          break
        case 'editor':
          this.operationType = 'editor'
          this.dialogVisible = true
          this.getAccountDetail(row.id)
          break
        case 'delete':
          this.getAccountDelete(row.id)
          break
        case 'enable':
          this.getAccountMotiy(row)
      }
    },
    // 启用
    getAccountMotiy(row) {
      const roles = []
      var valueT = ''
      valueT = row.state == 0 ? '启用' : '禁用'
      roles.push(row.id)
      this.$confirm(`是否${valueT}该账号！`, `${valueT}`, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        const form = {
          id: row.id,
          state: row.state == 0 ? 1 : 0
        }
        API.getAccountMotiy(form)
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    //删除
    getAccountDelete(id) {
      const ids = []
      ids.push(id)
      this.$confirm('删除以后数据不可恢复，请谨慎操作！', '删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        API.getAccountDelete({ ids: ids })
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    //获取详情数据
    getAccountDetail(id) {
      API.getAccountDetail({ id: id }).then(res => {
        if (res.status == 200) {
          this.addForm = res.data
          this.addForm.password = '123456'
          this.addForm.checkPass = '123456'
        } else {
          this.$message.error(res.message)
        }
      })
    },
    getTableData(init) {
      this.loading = true
      if (init) {
        this.searchForm.pageNum = 1
        this.searchForm.pageSize = 10
      }
      const form = JSON.parse(JSON.stringify(this.searchForm))

      if (!form.account) {
        delete form.account
      }
      API.getAcountList(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data.records
            this.tableData.forEach(item => {
              item.roleName = []
              if (item.roles.length > 0) {
                item.roles.forEach(citem => {
                  item.roleName.push(citem.name)
                })
              }
            })
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-table-column--selection .cell {
  padding-left: 10px;
}
#code {
  height: 500px;
  width: 500px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border: none;
}
.detailData {
  margin-bottom: 40px;
  div {
    line-height: 30px;
  }
}
::v-deep .el-upload-list {
  max-height: 400px;
  overflow: auto;
}
.mr50 {
  margin-right: 60px;
  width: 250px;
}
::v-deep .el-upload-dragger {
  width: 96px;
  height: 96px;
}
::v-deep .el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
::v-deep .el-upload--picture-card {
  width: 96px;
  height: 96px;
  line-height: 96px;
}

.labelWidth {
  width: 400px !important;
}
</style>
